import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { TreeView, TreeItem } from '@material-ui/lab';
import { Typography, makeStyles } from '@material-ui/core';
import {
  ChevronRight,
  ExpandMore,
  Class,
  MenuBook,
  Assignment,
  Assessment,
} from '@material-ui/icons';

const useTreeItemStyles = makeStyles((theme) => ({
  root: {
    color: theme.palette.text.secondary,
    '&:hover > $content': {
      backgroundColor: theme.palette.action.hover,
    },
    '&:focus > $content, &$selected > $content': {
      backgroundColor: `var(--tree-view-bg-color, ${theme.palette.grey[400]})`,
      color: 'var(--tree-view-color)',
    },
    '&:focus > $content $label, &:hover > $content $label, &$selected > $content $label': {
      backgroundColor: 'transparent',
    },
  },
  content: {
    color: theme.palette.text.secondary,
    borderTopRightRadius: theme.spacing(2),
    borderBottomRightRadius: theme.spacing(2),
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    '$expanded > &': {
      fontWeight: theme.typography.fontWeightRegular,
    },
  },
  group: {
    marginLeft: 0,
    '& $content': {
      paddingLeft: theme.spacing(2),
    },
  },
  expanded: {},
  selected: {},
  label: {
    fontWeight: 'inherit',
    color: 'inherit',
  },
  labelRoot: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0.5, 0),
  },
  labelIcon: {
    marginRight: theme.spacing(1),
  },
  labelText: {
    fontWeight: 'inherit',
    flexGrow: 1,
  },
}));

function StyledTreeItem({ Icon, text, ...rest }) {
  const classes = useTreeItemStyles();
  return (
    <TreeItem
      label={
        <div className={classes.labelRoot}>
          <Icon color="inherit" className={classes.labelIcon} />
          <Typography variant="body2" className={classes.labelText}>
            {text}
          </Typography>
        </div>
      }
      classes={{
        root: classes.root,
        content: classes.content,
        expanded: classes.expanded,
        selected: classes.selected,
        group: classes.group,
        label: classes.label,
      }}
      {...rest}
    />
  );
}
StyledTreeItem.propTypes = {
  Icon: PropTypes.elementType.isRequired,
  text: PropTypes.string.isRequired,
};

/**
 *
  <h3>Syllabus</h3>
  <p>
    Has optional hidden syllabus items (ie answer key, future
    lessons)
  </p>
 */

const useStyles = makeStyles({
  root: {
    // height: 264,
    flexGrow: 1,
    // maxWidth: 400,
  },
});

export function Syllabus() {
  const classes = useStyles();
  const [expanded, expandedSet] = useState([]);
  const [selected, selectedSet] = useState([]);
  const onNodeToggleSyllabus = (e, nodeIds) => expandedSet(nodeIds);
  const onNodeSelectSyllabus = (e, nodeIds) => selectedSet(nodeIds);
  return (
    <>
      <Typography variant="h6">Calculus I</Typography>
      <TreeView
        className={classes.root}
        defaultCollapseIcon={<ExpandMore />}
        defaultExpandIcon={<ChevronRight />}
        expanded={expanded}
        selected={selected}
        onNodeToggle={onNodeToggleSyllabus}
        onNodeSelect={onNodeSelectSyllabus}
      >
        <StyledTreeItem
          nodeId="1"
          text="Functions. New Functions from old"
          Icon={Class}
        >
          <StyledTreeItem
            nodeId="5"
            text="Read §1.1, 1.2, 1.3"
            Icon={MenuBook}
          />
          <StyledTreeItem nodeId="6" text="Homework" Icon={Assignment} />
          <StyledTreeItem nodeId="7" text="Quiz" Icon={Assessment} />
        </StyledTreeItem>

        <StyledTreeItem nodeId="2" text="Trigonometric functions." Icon={Class}>
          <StyledTreeItem nodeId="9" text="Homework" Icon={Assignment} />
        </StyledTreeItem>
        <StyledTreeItem
          nodeId="3"
          text="Exponential function, inverse functions, logarithms."
          Icon={Class}
        >
          <StyledTreeItem nodeId="10" text="Read §1.4, 1.5" Icon={MenuBook} />
        </StyledTreeItem>
        <StyledTreeItem
          nodeId="4"
          text="Derivative: motivation. Informal definition of limit."
          Icon={Class}
        >
          <StyledTreeItem nodeId="11" text="Read §2.1, 2.2" Icon={MenuBook} />
          <StyledTreeItem nodeId="12" text="Homework" Icon={Assignment} />
          <StyledTreeItem nodeId="13" text="Quiz" Icon={Assessment} />
        </StyledTreeItem>
      </TreeView>
    </>
  );
}
