// import { useHistory, useLocation } from 'react-router-dom';
import { navigate } from '@reach/router';
import PropTypes from 'prop-types';
import React, { createContext, useCallback, useContext, useState } from 'react';
import { useMutation } from 'urql';
import { AuthLogout } from '../graphql/authMutations';

const AuthContext = createContext({
  user: {},
  Login: (authPayload) => {},
  Logout: (from) => {},
});
// AuthContext.Provider.propTypes = {
//   user: PropTypes.shape({
//     username: PropTypes.string.isRequired,
//     type: PropTypes.string.isRequired,
//     companyName: PropTypes.string.isRequired,
//     isOwner: PropTypes.bool.isRequired,
//   }),
//   Login: PropTypes.func.isRequired,
//   Logout: PropTypes.func.isRequired,
// };
export function useContextAuth() {
  return useContext(AuthContext);
}
/**
 * Stores auth data simiilar to redux.
 * @param {Object} param0 Contains children
 */
export function AuthProvider({ children }) {
  console.log('AuthProvider ran');
  // setup user status
  const [user, userSet] = useState(
    JSON.parse(localStorage.getItem('user')) || {},
  );
  // const { replace } = useHistory();
  // const {
  //   state: { from },
  // } = useLocation();
  const Login = useCallback((authPayload) => {
    console.log('Logging in', { authPayload });
    // convert array to obj for query
    const {
      user: {
        username,
        type,
        Company: { name: companyName },
      },
    } = authPayload;
    const userSave = { username, type, companyName, isOwner: type >= 20 };
    // console.log({ userSave });
    localStorage.setItem('user', JSON.stringify(userSave));
    userSet(userSave);
    // routeToDefault ? history.push(DEFAULTROUTE) : history.goBack();
  }, []);
  const [, mutateLogout] = useMutation(AuthLogout);
  /**
   * Memoized function that Logouts user; Executes mutation that requests
   * server to remove auth cookies (ie: there is no known way for javascript to
   * remove http only cookies); Removes user from localstorage and
   * user state, indicating to app that user is not logged in; Redirects user
   * to /login with a from path;
   */
  const Logout = useCallback(
    (from) => {
      console.log('Logging out');
      mutateLogout(); // no async needed since ignoring response
      localStorage.removeItem('user');
      userSet({});
      // replace(from || '/');
      navigate(from || '/auth');
    },
    [mutateLogout],
  );
  return (
    <AuthContext.Provider value={{ user, Login, Logout }}>
      {children}
    </AuthContext.Provider>
  );
}

AuthProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
