import gql from 'graphql-tag';

export const AuthLogin = gql`
  mutation($input: AuthLogin!) {
    AuthLogin(input: $input) {
      user {
        username
        type
        Company {
          name
        }
      }
    }
  }
`;
export const AuthRegisterInit = gql`
  mutation($input: AuthRegisterInit!) {
    AuthRegisterInit(input: $input)
  }
`;
export const AuthRegisterValid = gql`
  mutation($input: AuthRegisterValid!) {
    AuthRegisterValid(input: $input) {
      user {
        username
        type
        Company {
          name
        }
      }
    }
  }
`;
export const AuthResetPasswordInit = gql`
  mutation($input: AuthResetPasswordInit!) {
    AuthResetPasswordInit(input: $input)
  }
`;
export const AuthResetPasswordValid = gql`
  mutation($input: AuthResetPasswordValid!) {
    AuthResetPasswordValid(input: $input) {
      user {
        username
        type
        Company {
          name
        }
      }
    }
  }
`;
export const AuthLogout = gql`
  mutation {
    AuthLogout
  }
`;
export const AuthUniqueCheck = gql`
  mutation($input: AuthUniqueCheck!) {
    AuthUniqueCheck(input: $input)
  }
`;
export const AuthSettings = gql`
  mutation($input: AuthSettings!) {
    AuthSecureM {
      AuthSettings(input: $input) {
        user {
          firstName
          lastName
          username
          email
          secured
          created
          updated
        }
      }
    }
  }
`;
