import { Snackbar } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import PropTypes from 'prop-types';
import React, {
  createContext,
  useCallback,
  useContext,
  useMemo,
  useState,
} from 'react';

const AlertContext = createContext({
  alerts: [],
  alertAdd: () => {},
  alertClose: () => {},
});
export function useContextAlert() {
  return useContext(AlertContext);
}
export function Alerts() {
  const { alerts, alertClose } = useContextAlert();
  const { open, id, message, severity, duration } = useMemo(() => {
    const open = alerts.length > 0;
    if (!open) return { open };
    return { open, ...alerts[0] };
  }, [alerts]);
  console.log('alert > Alerts:', alerts, open);
  return (
    <Snackbar
      key={id}
      autoHideDuration={duration}
      open={open}
      onClose={alertClose}
    >
      <MuiAlert
        elevation={6}
        variant="filled"
        severity={severity}
        onClose={alertClose}
      >
        {message}
      </MuiAlert>
    </Snackbar>
  );
}
let ctr = 0;
export function AlertProvider({ children }) {
  const [alerts, alertsSet] = useState([]);
  const alertAdd = useCallback(
    (message, severity = 'success', duration = 3000) => {
      // alertsSet((alerts) => [...alerts, { id: ctr++, message, severity, duration }]);
      alertsSet([{ id: ctr++, message, severity, duration }]);
    },
    [],
  );
  const alertClose = useCallback((event, reason) => {
    console.log('alert > Alerts > alertClose:', reason);
    if (reason === 'clickaway') {
      return;
    }
    alertsSet((alerts) => {
      const nA = [...alerts];
      nA.shift();
      return nA;
    });
  }, []);
  return (
    <AlertContext.Provider value={{ alerts, alertAdd, alertClose }}>
      {children}
    </AlertContext.Provider>
  );
}

AlertProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
