import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Tabs, Tab } from '@material-ui/core';
import { ChatQueue } from './ChatQueue';
import { ChatText } from './ChatText';

const A11yProps = (index) => ({
  id: `full-width-tab-${index}`,
  'aria-controls': `full-width-tabpanel-${index}`,
});
const TabPanel = ({ index, value, children }) => {
  if (index !== value) return null;
  return <div>{children}</div>;
};
TabPanel.propTypes = {
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
  children: PropTypes.node.isRequired,
};
/**
 * <p>Allow users to get access to hoster display</p>
    <p>Q and A mode</p>
    <p>
      Multi-User pointer - allow another user to point or write on
      screen
    </p>
 */
export function ChatSidebar() {
  const [tab, tabSet] = useState(0);
  const onChangeTab = (e, value) => tabSet(value);
  return (
    <>
      <Tabs value={tab} onChange={onChangeTab} variant="fullWidth">
        <Tab label="Queue" {...A11yProps(0)} />
        <Tab label="Chat" {...A11yProps(1)} />
        <Tab label="Q&amp;A" {...A11yProps(2)} />
      </Tabs>
      <TabPanel value={tab} index={0}>
        <ChatQueue />
      </TabPanel>
      <TabPanel value={tab} index={1}>
        <ChatText />
      </TabPanel>
      <TabPanel value={tab} index={2}>
        <ChatQueue />
      </TabPanel>
    </>
  );
}
