import React, { useState } from 'react';
import {
  Grid,
  Button,
  Card,
  CardHeader,
  Checkbox,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
  FormControl,
  InputLabel,
  NativeSelect,
} from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  cardHeader: {
    padding: theme.spacing(1, 2),
  },
  list: {
    // width: 200,
    height: 230,
    backgroundColor: theme.palette.background.paper,
    // overflow: 'auto',
  },
  // mainGrid: {
  //   width: '100%',
  // },
}));

function not(a, b) {
  return a.filter(({ id }) => b.indexOf(({ id: id2 }) => id2 === id) === -1);
}

function intersection(a, b) {
  return a.filter(({ id }) => b.indexOf(({ id: id2 }) => id2 === id) !== -1);
}

function union(a, b) {
  return [...a, ...not(b, a)];
}
const mockPending = [
  {
    id: '0',
    email: 'darthvader@starwars.com',
    name: 'Darth Vader',
  },
  {
    id: '1',
    email: 'kyloren@starwars.com',
    name: 'Kylo Ren',
  },
  {
    id: '2',
    email: 'bobafett@starwars.com',
    name: 'Boba Fett',
  },
];
const _Users = [
  { id: 'Pending', title: 'Pending' },
  { id: 'Active', title: 'Active' },
  { id: 'Blocked', title: 'Blocked' },
];
const _ActionsPending = [
  { id: 'Accept', title: 'Accept' },
  { id: 'Reject', title: 'Reject' },
  { id: 'Block', title: 'Block' },
];
const _ActionsActive = [
  { id: 'Reject', title: 'Reject' },
  { id: 'Block', title: 'Block' },
];
const _ActionsBlocked = [
  { id: 'Accept', title: 'Accept' },
  { id: 'Reject', title: 'Reject' },
];
function GenerateSelect(id, options, value, onChange) {
  return (
    <FormControl>
      <InputLabel htmlFor={id}>{id}</InputLabel>
      <NativeSelect
        value={value}
        onChange={onChange}
        inputProps={{ name: 'name', id }}
      >
        {options.map(({ id, title }) => (
          <option key={id} value={id}>
            {title}
          </option>
        ))}
      </NativeSelect>
    </FormControl>
  );
}
export function ChatQueue() {
  const classes = useStyles();
  const [users, usersSet] = useState('Pending');
  const [actionOptions, actionOptionsSet] = useState(_ActionsPending);
  const [actions, actionsSet] = useState('Accept');
  const [checked, setChecked] = useState([]);
  const [items, itemsSet] = useState(mockPending);
  const onChangeUsers = ({ target: { value } }) => {
    usersSet(value);
    switch (value) {
      case 'Pending':
        actionOptionsSet(_ActionsPending);
        actionsSet('Accept');
        break;
      case 'Active':
        actionOptionsSet(_ActionsActive);
        actionsSet('Reject');
        break;
      case 'Blocked':
        actionOptionsSet(_ActionsBlocked);
        actionsSet('Accept');
        break;

      default:
        throw new Error(`onChangeUsers invalid users value ${value}`);
    }
  };
  const onChangeActions = ({ target: { value } }) => actionsSet(value);
  const handleToggle = (item) => () => {
    const currentIndex = checked.findIndex(({ id }) => id === item.id);
    const newChecked = [...checked];

    if (currentIndex === -1) {
      newChecked.push(item);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setChecked(newChecked);
  };

  const numberOfChecked = (items) => intersection(checked, items).length;

  const handleToggleAll = (items, nItems, itemsL) => () => {
    if (nItems === itemsL) {
      setChecked(not(checked, items));
    } else {
      setChecked(union(checked, items));
    }
  };

  const nItems = numberOfChecked(items);
  const itemsL = items.length;
  return (
    <>
      <Grid
        container
        direction="row"
        justify="space-evenly"
        alignItems="center"
      >
        <Grid item>
          {GenerateSelect('Users', _Users, users, onChangeUsers)}
        </Grid>
        <Grid item>
          {GenerateSelect('Actions', actionOptions, actions, onChangeActions)}
        </Grid>
        <Grid item>
          <Button variant="contained" color="primary">
            Run
          </Button>
        </Grid>
      </Grid>
      <Card>
        <CardHeader
          className={classes.cardHeader}
          avatar={
            <Checkbox
              onClick={handleToggleAll(items, nItems, itemsL)}
              checked={nItems === itemsL && itemsL !== 0}
              indeterminate={nItems !== itemsL && nItems !== 0}
              disabled={itemsL === 0}
              inputProps={{ 'aria-label': 'all items selected' }}
            />
          }
          title={users}
          subheader={`${nItems}/${itemsL} selected`}
        />
        <Divider />
        <List className={classes.list} dense role="list">
          {items.map((item) => {
            const { id: id2, name, email } = item;
            const labelId = `transfer-list-all-item-${id2}-label`;

            return (
              <ListItem
                key={id2}
                role="listitem"
                button
                onClick={handleToggle(item)}
              >
                <ListItemIcon>
                  <Checkbox
                    checked={checked.findIndex(({ id }) => id2 === id) !== -1}
                    tabIndex={-1}
                    disableRipple
                    inputProps={{ 'aria-labelledby': labelId }}
                  />
                </ListItemIcon>
                {/* <Card>
                <CardHeader title={name} subheader={email} />
              </Card> */}
                <ListItemText id={labelId} primary={`${name} - ${email}`} />
              </ListItem>
            );
          })}
          <ListItem />
        </List>
      </Card>
    </>
  );
}
